.contact {
  height: 100vh;
  width: 100%;
  background: rgb(248, 235, 235);
}
a {
  color: rgb(82, 76, 76);
  text-decoration: none;
}
.inputs {
  width: 60%;
  height: 80%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  h1 {
    color: rgb(82, 76, 76);
  }

  input {
    width: 100%;
    align-items: center;
    padding: 20px;
    margin-block: 0.5rem;
    outline: none;
    border: none;
  }

  .message {
    padding-bottom: 10rem;
  }

  button {
    padding: 10px 40px;
    background: none;
    cursor: pointer;
    margin-top: 1rem;
    border: 1px solid pink;
    color: rgb(255, 155, 172);
    text-transform: uppercase;
  }
}
